    grid-gap: var(--dl-layout-space-oneandhalfunits);
    grid-template-columns: 1fr;
  }
  .thq-img-scale {
    width: 100%;
  }
  .navigation-desktop-links {
    display: none;
  }
  .navigation-mobile-toggle {
    display: block;
  }
  .footer-wrapper {
    padding-top: var(--spacing-3xl);
  }
  .footer-nav-column {
    flex: 1 1 45%;
  }
  .footer-contact-column {
    flex: 1 1 45%;
  }
  .footer-bottom-content {
    text-align: center;
    flex-direction: column;
  }
  .cta-split-wrapper {
    flex-direction: column;
  }
  .cta-image-column {
    height: 300px;
  }
  .cta-inner-content {
    margin: 0 auto;
    text-align: center;
  }
  .cta-image-overlay {
    background: linear-gradient(to bottom, var(--color-primary), transparent);
  }
  .cta-buttons {
    justify-content: center;
  }
}
 
@media(max-width: 479px) {
  .thq-section-padding {
    padding: var(--dl-layout-space-oneandhalfunits);
  }
  .thq-flex-column {
    gap: var(--dl-layout-space-unit);
  }
  .thq-flex-row {
    gap: var(--dl-layout-space-unit);
  }
  .thq-grid-6 {
    grid-gap: var(--dl-layout-space-unit);
    grid-template-columns: 1fr 1fr;
  }
  .thq-grid-5 {
    grid-gap: var(--dl-layout-space-unit);
    grid-template-columns: 1fr 1fr;
  }
  .thq-grid-3 {
    grid-gap: var(--dl-layout-space-unit);
    align-items: center;
    grid-template-columns: 1fr;
  }
  .thq-grid-4 {
    grid-gap: var(--dl-layout-space-unit);
    align-items: center;
    flex-direction: column;
    grid-template-columns: 1fr;
  }
  .thq-grid-2 {
    grid-gap: var(--dl-layout-space-unit);
  }
  .thq-grid-auto-300 {
    grid-template-columns: 1fr;
  }
  .navigation-logo-icon {
    width: 32px;
    height: 32px;
  }
  .navigation-brand-name {
    font-size: var(--font-size-lg);
  }
  .footer-nav-column {
    flex: 1 1 100%;
  }
  .footer-contact-column {
    flex: 1 1 100%;
  }
  .footer-legal-links {
    gap: var(--spacing-lg);
    width: 100%;
    justify-content: center;
  }
  .footer-brand-name {
    font-size: var(--font-size-xl);
  }
  .home-hero-title {
    font-size: var(--font-size-3xl);
  }
  .services-grid {
    grid-template-columns: 1fr;
  }
  .team-grid {
    grid-template-columns: 1fr;
  }
  .step-item {
    flex: 0 0 100%;
  }
  .stats-grid {
    grid-template-columns: 1fr;
  }
  .testimonials-grid {
    grid-template-columns: 1fr;
  }
}
